
.title {
  padding: 0.44rem;
  background: #fff;
  text-align: center;
  font-size: 0.426667rem;
  color: #010101;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.title .nameTxt {
  max-width: 80%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.toast {
  position: fixed;
  left: 50%;
  top: 50%;
  background: #000;
  padding: 0.266667rem;
  font-size: 0.346667rem;
  border-radius: 0.133333rem;
  transform: translateX(-50%);
  color: #fff;
}
.serBox {
  background: #F5F5F5;
  padding: 0.266667rem 0.426667rem;
}
.serBox div {
  background: #fff;
  height: 0.96rem;
  border-radius: 0.133333rem;
}
.listBox {
  background: #fff;
  padding: 0 0.48rem;
}
.listBox li {
  padding: 0.373333rem 0;
  border-bottom: 0.013333rem #f2f2f2 solid;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  position: relative;
}
.listBox li .delete {
  width: 0.413333rem;
  height: 0.413333rem;
  background: url("../assets/img/sc.png") no-repeat;
  background-size: cover;
  position: absolute;
  top: 0.373333rem;
  right: 0;
  z-index: 999;
}
.listBox li .turn {
  width: 0.426667rem;
  height: 0.426667rem;
  background: url("../assets/img/zr.png") no-repeat;
  background-size: cover;
  position: absolute;
  top: 0.373333rem;
  right: 1.133333rem;
  z-index: 999;
}
.listBox li .left {
  width: 1.6rem;
}
.listBox li .left img {
  width: 100%;
  border-radius: 50%;
}
.listBox li .right {
  width: calc(100% - 2rem);
}
.listBox li .right h5 {
  color: #000;
  font-size: 0.48rem;
  font-weight: bold;
  margin-bottom: 0.533333rem;
}
.listBox li .right p {
  font-size: 0.32rem;
  color: #999;
}
.listBox li .right p span {
  font-size: 0.373333rem;
}
.listBox li:last-child {
  border-bottom: none;
}
.groupUser .van-search {
  padding: 0.266667rem 0.48rem;
}
.groupUser .van-cell {
  line-height: 0.96rem;
}
.groupUser .van-search .van-cell {
  border-radius: 0.133333rem;
}
.groupUser .van-field__control {
  font-size: 0.48rem;
  text-align: center;
}
.groupUser .van-cell__left-icon,
.groupUser .van-cell__right-icon {
  line-height: 0.96rem !important;
  font-size: 0.533333rem !important;
}
.groupUser .hint {
  margin-top: 2.666667rem;
  color: #666;
  text-align: center;
  font-size: 0.533333rem;
}


