
@import '../assets/iconfont.css';
.group {
  background: #f5f5f5;
  height: 100%;
  overflow-y: scroll;
}
.group .g-size {
  font-size: 0.453333rem;
  font-weight: 400;
}
.group .fold {
  height: 1.28rem;
  display: -ms-flexbox;
  display: flex;
  padding: 0 0.48rem;
  line-height: 1.28rem;
  background: #fff;
  -ms-flex-pack: justify;
  justify-content: space-between;
  font-size: 0.426667rem;
  color: #000;
  margin-bottom: 0.213333rem;
}
.group .fold i {
  margin: auto 0;
}
.group .none {
  margin-bottom: 0;
}
.group .ma10 {
  margin-bottom: 0.213333rem;
}
.group .group-p {
  padding: 0 0.48rem;
  background: #fff;
}
.group .group-p .group-list {
  height: 2.24rem;
  box-sizing: border-box;
  border-bottom: 0.013333rem solid #f2f2f2;
}
.group .group-p .group-list .group-img {
  height: 1.066667rem;
  width: 1.066667rem;
  border-radius: 50%;
  margin-top: 0.266667rem;
  margin-right: 0.426667rem;
  overflow: hidden;
}
.group .group-p .group-list .group-img img {
  height: 100%;
}
.group .group-p .group-list .group-name {
  margin-top: 0.333333rem;
  font-size: 0.48rem;
  color: #000;
}
.group .group-p .group-list .group-name p {
  width: 7.333333rem;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.group .group-p .group-list .group-name .member {
  font-size: 0.32rem;
  margin-top: 0.066667rem;
  color: #999;
}
.group .group-p .group-list .group-name .member img {
  width: 0.36rem;
  height: 0.32rem;
  margin-right: 0.186667rem;
}
.group .group-p .g-h {
  height: 1.68rem;
}


